<template>
  <div class="mlist">
    <criteria-paged :model="model" v-ref:paged style="font-size:12px;">
      <criteria partial='criteria' v-ref:pp  @condition-changed='search' >
        <div novalidate class="form-horizontal" partial>
        </div>
      </criteria>
      <data-grid :model="model" partial='list' class="grid auto" v-ref:grid  class="col-md-12" >
        <template partial='head'>
          <tr>
            <th style="text-align:center;"><nobr>气量</nobr></th>
            <th style="text-align:center;"><nobr>金额</nobr></th>
            <th style="text-align:center;"><nobr>售气网点</nobr></th>
            <th style="text-align:center;"><nobr>购气时间</nobr></th>
          </tr>
        </template>
        <template partial='body' >
          <td style="text-align:center;"><nobr>{{row.f_pregas}}</nobr></td>
          <td style="text-align:center;"><nobr>{{row.f_preamount}}</nobr></td>
          <td style="text-align:center;"><nobr>{{row.f_outlets == "兴业银行" || row.f_outlets =="晋城银行" || row.f_outlets =="农商银行" ? row.f_operator : row.f_outlets}}</nobr></td>
          <td style="text-align:center;"><nobr>{{row.f_delivery_date}}</nobr></td>
        </template>
      </data-grid>
    </criteria-paged>
  </div>
</template>
<script>
import { PagedList } from 'vue-client'
export default {
  title: '机表用户列表',
  data () {
    return {
      model: new PagedList(config.chargeUrl + 'rs/sql/singleTable_OrderBy', 5, {items: "'*'", tablename: "'t_sellinggas'", orderitem: "'f_delivery_date desc'"}),
      isaffrim: false,
      userInfo: {}
    }
  },
  props: ['info'],
  ready () {
    this.model.search('f_user_id = ' + this.info.f_user_id + ' and f_charge_state = \'有效\'', {})
  }
}
</script>



